.mt{
  margin-top: 60px!important;
}

.brand-text{
  font-family: 'Pacifico', cursive;
  font-size: 40px;
}
.color-txt{
   font-family: 'Pacifico', cursive;
   font-size: 15px;
}
.tutti{
  color: #8B3D2A;
}
.frutti{
  color: #428640;
}
.utf{
  height: 160px;
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;

}
.utf-click{
  height: 160px;
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;
  border-style: solid;
  border-width: 4px;
  border-color: #222222;
}

.mtf{
  height: 160px;

}
.mtf-clicked{
  height: 160px;
  border-style: solid;
  border-width: 4px;
  border-color: #222222;
}

.htf{
  height: 105px;
  width: 40px!important;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  margin-bottom: 30px;
  
}
.round:hover{
  border-width: 3px;
  border-style: solid;
  border-color:#444444;
  cursor:pointer;
}
.m-msg{

  color: black!important;
  margin-top: 0px!important;
  font-family: 'Pacifico', cursive;
  font-size: 30px;
}
.made{
  font-weight: 600;
}